












import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";

@Component
export default class DetailButton extends Vue {
  @Prop(String) readonly value!: string
  select(type: string) {
    if (type !== "-" && type !== "+") {
      throw new Error("Type is unKnow");
    }
    this.$emit('update:value',type)
  }
}

