




















import Vue from "vue";
import detailTitle from "@/components/Detail/detailTitle.vue";
import DetailButton from "@/components/Detail/DetailButton.vue";
import { Component } from "vue-property-decorator";
import clone from "@/lib/clone";
import dayjs from "dayjs";

type RootItem = {
  recordList: RecordItem[];
};

@Component({ components: { DetailButton, detailTitle } })
export default class Detail extends Vue {
  type = "-";

  created() {
    this.$store.commit("fetchRecords");
  }

  get recordList() {
    return (this.$store.state as RootItem).recordList;
  }

  get groupList() {
    const { recordList } = this;
    if (recordList.length === 0) {
      return [];
    }
    const newList = clone(recordList)
      .filter(item => item.type === this.type)
      .sort((a, b) => dayjs(b.timeAt).valueOf() - dayjs(a.timeAt).valueOf());
    type Result = { title: string; total?: number; items: RecordItem[] }[];
    const result: Result = [
      {
        title: dayjs(newList[0].timeAt).format("YYYY-MM-DD"),
        items: [newList[0]]
      }
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.timeAt), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.timeAt).format("YYYY-MM-DD"),
          items: [current]
        });
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        return sum + item.amount;
      }, 0);
    });
    return result;
  }

  onchange(value: string) {
    this.type = value;
  }
}
