
























import Vue from "vue";
import router from "@/router";
import { Component } from "vue-property-decorator";
import clone from "@/lib/clone";
import dayjs from "dayjs";

type RootItem = {
  recordList: RecordItem[];
};

@Component
export default class Bill extends Vue {
  year = "";
  inMoney = "0.00";
  outMoney = "0.00";
  n = "";

  toDetail() {
    router.push("detail");
  }

  mounted() {
    this.$store.commit("fetchRecords");
    const years = new Date().getUTCFullYear();
    this.year = years.toString();
    const month = new Date().getMonth() + 1;
    this.n = month.toString();
  }

  get recordList() {
    return (this.$store.state as RootItem).recordList;
  }

  get moneyList() {
    const { recordList } = this;
    if (recordList.length === 0) {
      return [];
    }
    const newList = clone(recordList).sort(
      (a, b) => dayjs(b.timeAt).valueOf() - dayjs(a.timeAt).valueOf()
    );
    const nowMonth = newList.filter(item =>
      dayjs(item.timeAt).isSame(dayjs().valueOf(), "year")
    );
    const nowMonth2 = nowMonth.filter(item =>
      dayjs(item.timeAt).isSame(dayjs().valueOf(), "month")
    );
    this.inMoney = nowMonth2
      .filter(item => item.type === "+")
      .reduce((sum, items) => {
        return sum + items.amount;
      }, 0)
      .toString();
    this.outMoney = nowMonth2
      .filter(item => item.type === "-")
      .reduce((sum, items) => {
        return sum + items.amount;
      }, 0)
      .toString();
    return nowMonth2;
  }
}
